import React, { useEffect } from 'react';
import AsyncSelectJs from 'shared/components/Fields/AsyncSelectJs';
import { KntTwilioStyle } from 'shared/services';
import { Field } from 'formik';
import { useAppState } from 'shared/state';
import { KntInputField } from 'shared/components/Fields';
import { InviteClientFormFooter } from '../../../../ClientsManagement/components/InviteClientFormFooter';
import { useSwitch } from 'shared/hooks';
import { IOSSwitch } from 'shared/components/Elements/Fields/SwitchComp';
import { FormControlLabel } from 'shared/components/Elements';

type Props = {
	isSubmitting: boolean;
	onSuccess: () => void;
	isEditing?: boolean;
	setFieldValue: any;
};
function ClientGroupElements({ isSubmitting, onSuccess, setFieldValue, isEditing = false }: Props) {
	const { companyId } = useAppState();
	const { checked, handleChange } = useSwitch({ open_for_cases: false });

	useEffect(() => {
		setFieldValue('open_for_cases', checked.open_for_cases);
	}, [checked]);

	return (
		<div className="w-full">
			<Field
				label="Campaign Name:"
				labelClasses="ml-6 text-xs font-light mb-2 text-kgrey-welcome block"
				name="name"
				component={KntInputField}
				placeholder="Type your campaign name.."
				required
				autoComplete="off"
				maxLength={'250'}
			/>
			<FormControlLabel
				control={
					<IOSSwitch
						checked={checked.open_for_cases}
						onChange={handleChange}
						name="open_for_cases"
					/>
				}
				label="Allow Case Creation"
				className="!mb-4"
			/>
			<>
				<label className="ml-6 mb-2 block text-xs font-light text-kgrey-welcome">
					<span>Search Client:</span>
				</label>
				<div className="react-select-dropdown">
					<Field
						component={AsyncSelectJs}
						customStylesProps={KntTwilioStyle}
						name="categories"
						placeholder="Select Clients Groups here"
						optionLabel="name"
						optionValue="id"
						isSearchable
						isMulti
						isClearable
						loadOptionsKey="results"
						loadOptionsUrl={`/${companyId}/categories/`}
						loadOptionsParams={(search: string) => ({
							extra: {
								search
							}
						})}
					/>
				</div>
			</>
			<div className="flex-center-between mt-8">
				<InviteClientFormFooter
					onSuccess={onSuccess}
					isSubmitting={isSubmitting}
					submitText={`${!isEditing ? 'Create' : 'Edit'} Campaign`}
				/>
			</div>
		</div>
	);
}

export default ClientGroupElements;
