import React, { useEffect, useState } from 'react';
import { useRouter, useSwitch, useUnreadCaseTab } from 'shared/hooks';
import { useNewsContext } from '../context/NewsContext';

export function useNewsManagement() {
	const [primeTab, setPrimeTab] = React.useState(0);
	const [pageName, setPageName] = React.useState('Clients List');

	const [groupVisible, setGroupVisible] = useState(false);
	const [campaignVisible, setCampaignVisible] = useState(false);

	const { location, match, history } = useRouter();
	const { url, path } = match;
	const { unreadCasesQuantity, isHidden } = useUnreadCaseTab('News');
	const { checked, handleChange } = useSwitch({ isStaff: false });
	const { setIsStaff } = useNewsContext();

	const handleChangePrime = (
		event: React.ChangeEvent<Record<string, unknown>>,
		newValue: number
	) => {
		setPrimeTab(newValue);
	};

	const toggleGroupForm = () => setGroupVisible((prev) => !prev);
	const toggleCampaignCreateForm = () => setCampaignVisible((prev) => !prev);

	const navigateToCreatePage = () => history.push('/news/create');
	const navigateToTemplateCreate = () => history.push(`/news/create-template`);
	const navigateToEditPage = (id: number) => () => history.push(`/news/edit/${id}`);

	//Just to set up tab active tab indication
	useEffect(() => {
		const locationSplitterArray = location.pathname.split('/').filter(Boolean).filter(String);
		const nestedPageType = locationSplitterArray[locationSplitterArray.length - 1] || '';

		(() => {
			switch (nestedPageType) {
				case 'news-list': {
					setPrimeTab(0);
					setPageName('News List');
					break;
				}
				case 'campaigns': {
					setPrimeTab(1);
					setPageName('Campaigns');
					break;
				}
				case 'templates': {
					setPrimeTab(2);
					setPageName('News Templates');
					break;
				}
				case 'groups': {
					setPrimeTab(3);
					setPageName('Client groups');
					break;
				}
				default:
					return null;
			}
		})();
	}, [location]);

	useEffect(() => {
		setIsStaff(checked.isStaff);
	}, [checked]);

	return {
		primeTab,
		pageName,
		url,
		path,
		unreadCasesQuantity,
		isHidden,
		handleChangePrime,
		groupVisible,
		toggleGroupForm,
		toggleCampaignCreateForm,
		navigateToCreatePage,
		navigateToEditPage,
		campaignVisible,
		navigateToTemplateCreate,
		location,
		handleChange,
		checked
	};
}
