import { useEntityActionPopup, useNotistack } from 'shared/hooks';
import { useAppState } from 'shared/state';
import { formRequest } from 'modules/entity/actions';
import { useDispatch } from 'react-redux';
import { NewsTemplateType } from 'shared/typings/news.types';
import { useCallback } from 'react';

export function useNewsTemplates() {
	const { companyId } = useAppState();
	const dispatch = useDispatch();
	const { showNotification } = useNotistack();
	const {
		visible: deleteVisible,
		showVisible: showDeleteVisible,
		loading: deleteLoading,
		toggleLoading: toggleDeleteLoading,
		closePopup: closeDeletePopup
	} = useEntityActionPopup<NewsTemplateType>();

	const handleTemplateDeleteRequest = useCallback((template: NewsTemplateType) => {
		//show loader
		toggleDeleteLoading();
		dispatch(
			formRequest({
				entity: 'templates',
				name: `AllTemplates`,
				method: 'delete',
				url: `/${companyId}/notification-templates/${template.id}/`,
				deleteData: true,
				id: template.id,
				cb: {
					success: () => {
						showNotification({ message: 'Successfully deleted', variant: 'success' });
					},
					error: (err) => {
						showNotification({ message: 'Something went wrong', variant: 'error' });
					},
					finally: () => {
						toggleDeleteLoading();
						closeDeletePopup();
					}
				},
				params: {},
				primaryKey: 'id',
				values: {}
			})
		);
	}, []);

	const deleteTemplateHandler = () => () => handleTemplateDeleteRequest(deleteVisible!);

	return {
		deleteLoading,
		deleteTemplateHandler,
		showDeleteVisible,
		closeDeletePopup,
		deleteVisible
	};
}
