import React from 'react';
import { useTranslation } from 'react-i18next';

type Props = {
	onSuccess: () => void;
	isSubmitting: boolean;
	disableOptions?: boolean;
	submitText?: string;
};

export function InviteClientFormFooter({
	onSuccess,
	isSubmitting,
	disableOptions = false,
	submitText = 'Invite Client'
}: Props) {
	const { t } = useTranslation();
	return (
		<>
			<button
				className={
					'mr-5 rounded-lg border border-kgrey  px-5 py-2 text-kgrey transition hover:border-primary hover:text-primary focus:ring focus:ring-primary'
				}
				onClick={onSuccess}
				type="button"
			>
				{t('cancel')}
			</button>
			<button
				className={
					'rounded-lg bg-primary-dark px-5 py-2 text-white ' +
					'border border-transparent hover:border-primary-dark' +
					' transition-colors hover:bg-white ' +
					'hover:text-primary-dark focus:ring focus:ring-primary disabled:pointer-events-none disabled:opacity-50 dark:hover:bg-primary-300 dark:hover:text-white dark:focus:ring-primary-300'
				}
				type="submit"
				disabled={isSubmitting || disableOptions}
			>
				{submitText}
			</button>
		</>
	);
}
