/** @jsxImportSource @emotion/react */
import React, { lazy, Suspense } from 'react';
import { StandardPageLayout } from 'shared/layouts';
import { Helmet } from 'react-helmet';
import { CustomMuiTabs, ErrorBoundary, KntButton, Loader } from '../../shared/components';
import { Link, Route, Switch } from 'react-router-dom';
import { NewsContextProvider } from './context/NewsContext';
import { useNewsManagement } from './hooks';
import { NewsSearchQuery } from './components/NewsSearchQuery';
import { WhiteModal } from '../../shared/components/Elements';
import { CreateCampaign } from './NestedPages/Campaigns/components';
import clsx from 'clsx';
import { useAppState } from '../../shared/state';
import { useAuth } from 'modules/auth/hooks/useAuth';
import { IOSSwitchDuo } from '../../shared/components/Elements/Fields/SwitchCompDuo';

export type NewsItemType = {
	created_date: string;
	favourite: boolean;
	files: any[];
	id: number;
	open_for_cases: boolean;
	short_body: string;
	subject: string;
	author: {
		name: string;
	};
	body?: string | undefined;
};

const MainNewsList = lazy(() => import('./NestedPages/Main'));
const Campaigns = lazy(() => import('./NestedPages/Campaigns'));
const ClientGroups = lazy(() => import('./NestedPages/ClientGroups'));
const CreateNews = lazy(() => import('./NestedPages/CreateNews'));
const EditRegularNews = lazy(() => import('./NestedPages/EditRegularNews'));
const NewsView = lazy(() => import('./NestedPages/NewsView'));
const CampaignView = lazy(() => import('./NestedPages/CampaignView'));
const CreateCampaignNews = lazy(() => import('./NestedPages/CreateCampaignNews'));
const EditCampaignNews = lazy(() => import('./NestedPages/EditCampaignNews'));
const Templates = lazy(() => import('./NestedPages/NewsTemplates'));
const CreateTemplate = lazy(() => import('./NestedPages/CreateTemplate'));
const EditTemplate = lazy(() => import('./NestedPages/EditTemplate'));
const TemplatesView = lazy(() => import('./NestedPages/TemplatesView'));

function a11yProps(index: number) {
	return {
		id: `news-tabpanel-${index}`,
		'aria-controls': `news-tab-${index}`
	};
}

function NewsInner() {
	const {
		primeTab,
		pageName,
		url,
		path,
		unreadCasesQuantity,
		isHidden,
		handleChangePrime,
		groupVisible,
		toggleGroupForm,
		toggleCampaignCreateForm,
		navigateToCreatePage,
		campaignVisible,
		navigateToTemplateCreate,
		location,
		handleChange,
		checked
	} = useNewsManagement();

	const { isPermitted } = useAppState();
	const { userData } = useAuth();
	const canSendNews = userData?.can_send_news;

	const pathnameList = [
		'campaigns/view',
		'campaigns/news',
		'/view',
		'/edit',
		'campaigns/edit',
		'campaigns/view',
		'news/create',
		'templates/edit',
		'templates/view'
	];

	const notBottomExtended = pathnameList.find((path) => location.pathname.includes(path));
	const isNewsCreatePage = location.pathname.includes('news/create');
	const isNewsEditPage = location.pathname.includes('edit');
	const isNewsListPage = location.pathname.includes('news-list');

	return (
		<StandardPageLayout>
			<div className="flex min-h-full max-w-full flex-grow flex-col p-4">
				{(!isHidden || unreadCasesQuantity === 0) && (
					<Helmet>
						<title>{`KENNEKT | ${pageName}`}</title>
					</Helmet>
				)}
				<div className="mb-5 flex items-center justify-between">
					<div className="flex w-full flex-wrap items-center justify-between">
						<div className="ml-auto flex items-center">
							{!Boolean(notBottomExtended) && <NewsSearchQuery />}
							{isNewsListPage && (
								<div className="flex-center-y ml-5">
									<FrequencyLabel title="Client News" />
									<IOSSwitchDuo checked={checked.isStaff} onChange={handleChange} name="isStaff" />
									<FrequencyLabel title="Staff News" />
								</div>
							)}
							{!isNewsCreatePage && !isNewsEditPage && (
								<NewsHeaderButtonGroup
									primeTab={primeTab}
									navigateToCreatePage={navigateToCreatePage}
									toggleCampaignCreateForm={toggleCampaignCreateForm}
									navigateToTemplateCreate={navigateToTemplateCreate}
									toggleGroupForm={toggleGroupForm}
									isPermitted={isPermitted}
									canSendNews={canSendNews}
								/>
							)}

							<WhiteModal
								handleModalOpen={toggleCampaignCreateForm}
								open={campaignVisible}
								maxWidth={446}
							>
								<CreateCampaign onSuccess={toggleCampaignCreateForm} />
							</WhiteModal>
							<WhiteModal handleModalOpen={toggleGroupForm} open={groupVisible} maxWidth={446}>
								<p>Client Group Create Form</p>
							</WhiteModal>
						</div>
					</div>
				</div>
				{!isNewsCreatePage && !isNewsEditPage && (
					<div
						className={clsx('prime-tabs', {
							'prime-tabs_no-bottom-extend': notBottomExtended
						})}
					>
						<CustomMuiTabs.Tabs
							roundedTop={!notBottomExtended}
							value={primeTab}
							// @ts-ignore
							onChange={handleChangePrime}
							aria-label="simple tabs"
						>
							<CustomMuiTabs.Tab
								label="News"
								component={Link}
								to={`${url}/news-list`}
								{...a11yProps(0)}
							/>
							<CustomMuiTabs.Tab
								label="Campaigns"
								component={Link}
								to={`${url}/campaigns`}
								{...a11yProps(1)}
							/>
							<CustomMuiTabs.Tab
								label="Templates"
								component={Link}
								to={`${url}/templates`}
								{...a11yProps(2)}
							/>
							<CustomMuiTabs.Tab
								label="Client Groups"
								component={Link}
								to="/clients-management/categories"
								{...a11yProps(3)}
							/>
						</CustomMuiTabs.Tabs>
					</div>
				)}

				<div className="relative flex max-w-full flex-grow flex-col">
					<div className="relative flex-grow">
						<Suspense fallback={<Loader tiled />}>
							<Switch>
								<Route exact path={`${path}/news-list`} component={MainNewsList} />
								<Route exact path={`${path}/create`} component={CreateNews} />
								<Route exact path="/news/edit/:newsId" component={EditRegularNews} />
								<Route exact path="/news/view/:newsId" component={NewsView} />
								<Route exact path={`${path}/campaigns`} component={Campaigns} />
								<Route
									exact
									path="/news/campaigns/news/create/:campaignId"
									component={CreateCampaignNews}
								/>
								<Route exact path="/news/campaigns/view/:campaignId" component={CampaignView} />
								<Route exact path="/news/campaigns/view/:campaignId/:newsId" component={NewsView} />
								<Route
									exact
									path="/news/campaigns/edit/:campaignId/:newsId"
									component={EditCampaignNews}
								/>
								<Route exact path={`${path}/templates`} component={Templates} />
								<Route exact path={`${path}/create-template`} component={CreateTemplate} />
								<Route
									exact
									path={`${path}/templates/view/:templateId`}
									component={TemplatesView}
								/>
								<Route exact path={`${path}/templates/edit/:templateId`} component={EditTemplate} />
								<Route exact path={`${path}/groups`} component={ClientGroups} />
							</Switch>
						</Suspense>
					</div>
				</div>
			</div>
		</StandardPageLayout>
	);
}

type NewsHeaderButtonGroupType = {
	primeTab: number;
	navigateToCreatePage: () => void;
	toggleCampaignCreateForm: () => void;
	navigateToTemplateCreate: () => void;
	toggleGroupForm: () => void;
	isPermitted: boolean;
	canSendNews: boolean;
};

function NewsHeaderButtonGroup({
	primeTab,
	navigateToCreatePage,
	toggleCampaignCreateForm,
	navigateToTemplateCreate,
	toggleGroupForm,
	isPermitted,
	canSendNews
}: NewsHeaderButtonGroupType) {
	return (
		<>
			{primeTab === 0 ? (
				<NewsMainButton handler={navigateToCreatePage} btnText="Create News" />
			) : null}
			{primeTab === 1 ? (
				<NewsMainButton handler={toggleCampaignCreateForm} btnText="Create New Campaign" />
			) : null}
			{primeTab === 2 ? (
				<NewsMainButton handler={navigateToTemplateCreate} btnText="Create New Template" />
			) : null}
			{primeTab === 3 ? (
				<NewsMainButton handler={toggleGroupForm} btnText="Create New Client Group" />
			) : null}
		</>
	);
}

type NewsButtonProps = {
	handler: any;
	btnText: string;
	isDisabled?: boolean;
};

function NewsMainButton({ handler, btnText, isDisabled = false }: NewsButtonProps) {
	return (
		<KntButton isDisabled={isDisabled} onClick={handler} className="ml-4 disabled:opacity-60">
			{btnText}
		</KntButton>
	);
}

export function News() {
	return (
		<ErrorBoundary>
			<NewsContextProvider>
				<NewsInner />
			</NewsContextProvider>
		</ErrorBoundary>
	);
}

type FrequencyLabelProps = {
	title: string;
};

function FrequencyLabel({ title }: FrequencyLabelProps) {
	return (
		<dl className="mr-3.5">
			<dd className="text-md font-semibold dark:text-white">{title}</dd>
		</dl>
	);
}
