import React from 'react';
import { CgSearch } from 'react-icons/cg';
import { useNewsContext } from '../context/NewsContext';

export function NewsSearchQuery() {
	const { searchQuery, setSearchQuery } = useNewsContext();

	return (
		<div className="group-sidebar__search relative">
			<input
				type="search"
				value={searchQuery}
				className={
					'group-sidebar__search-input w-36 border-b outline-none ' +
					'border-kgrey-border-solid dark:border-white' +
					' pl-7 transition focus:border-primary dark:text-white dark:focus:border-primary' +
					' placeholder-[rgba(45, 45, 45, 0.3)] pt-2 pb-2 dark:bg-kdark'
				}
				placeholder={'Search'}
				onChange={(e) => setSearchQuery(e.target.value)}
			/>
			<div className="absolute top-1/2 left-0 -translate-y-1/2">
				<div className="text-kgrey">
					<CgSearch />
				</div>
			</div>
		</div>
	);
}
