import React from 'react';
import { useAppState } from 'shared/state';
import { useNotistack } from 'shared/hooks';
import EntityForm from 'modules/entity/forms';
import { FormHeader, Loader } from 'shared/components';
import CampaignFormElements from './CampaignFormElements';
import { CampaignEntity } from 'shared/typings/news.types';

type Props = {
	onSuccess: () => void;
	entity: CampaignEntity;
};

export function EditCampaign({ onSuccess, entity }: Props) {
	const { companyId } = useAppState();
	const { showNotification } = useNotistack();

	if (!entity) {
		return null;
	}

	return (
		<div>
			<>
				<EntityForm.Main
					entity="campaigns"
					name="AllCampaigns"
					url={`/${companyId}/campaigns/${entity?.id}/`}
					method="put"
					primaryKey="id"
					sendAsFormData={false}
					normalizeData={(data: any) => data}
					updateData
					fields={[
						{
							name: 'categories',
							// onSubmitValue: (value) => get(value, 'id'),
							onSubmitKey: '',
							required: false,
							type: 'object',
							value: entity.categories
						},
						{
							name: 'name',
							onSubmitKey: '',
							required: true,
							value: entity.name
						},
						{
							name: 'open_for_cases',
							onSubmitKey: '',
							value: entity.open_for_cases,
							required: true
						}
					]}
					onSuccess={(response, resetForm): any => {
						showNotification({ message: 'Successfully edited..' });
						onSuccess();
						resetForm();
					}}
					onError={(error, resetForm): any => {
						const errorText = error?.response?.data?.message || 'Something went wrong';
						showNotification({ message: errorText, variant: 'error' });
					}}
				>
					{({ isSubmitting, values, resetForm, setFieldValue }) => {
						if (process.env.NODE_ENV === 'development') {
							console.log('edit campaign values', values);
						}
						return (
							<>
								<FormHeader resetForm={resetForm} title="Edit Templates" isEditing />
								<>
									<div className={'flex items-center'}>
										<CampaignFormElements
											isSubmitting={isSubmitting}
											onSuccess={onSuccess}
											setFieldValue={setFieldValue}
											isEditing
										/>
										{isSubmitting && <Loader tiled withOverlay />}
									</div>
								</>
							</>
						);
					}}
				</EntityForm.Main>
			</>
		</div>
	);
}
