import axios from 'axios';
import { useEffect, useState } from 'react';
import config from 'config';
import { useAppState } from 'shared/state';
import { useAuth } from '../../../modules/auth/hooks/useAuth';
import { useNewsContext } from '../context/NewsContext';

export function useUploadAudio() {
	const { companyId } = useAppState();
	const { token } = useAuth();
	const [state, setState] = useState<any>({
		uuids: {},
		audioCancelToken: null,
		audioUploadProgress: null
	});

	const { audioUploadProgress, audioCancelToken } = state;

	const { cancelAudioUpload, setAudioCancelSending, setAudioUploadProgress, setAudioResponse } =
		useNewsContext();

	const action = `${config.API_ROOT}/${companyId}/upload/`;

	//Setting progress of uploading data
	useEffect(() => {
		if (audioUploadProgress) {
			setAudioUploadProgress(audioUploadProgress);
		}
	}, [audioUploadProgress]);

	// Handle cancelling uploading audio
	useEffect(() => {
		if (cancelAudioUpload && audioCancelToken) {
			const errorMessage = 'Uploading canceled by the user.';
			audioCancelToken?.cancel(errorMessage);

			setAudioUploadProgress(null);
			setAudioCancelSending(false);
			setState((prev: any) => {
				return {
					...prev,
					audioCancelToken: null
				};
			});
		}
	}, [cancelAudioUpload]);

	const uploadAudioFile = (file: any) => {
		const formData = new FormData();
		const filename = `${Math.random()}`.slice(2) + '.mp3';
		formData.append('file', file, filename);
		const CancelToken = axios.CancelToken;
		const source = CancelToken.source();
		setState((prev: any) => {
			return {
				...prev,
				audioCancelToken: source
			};
		});

		axios
			.post(action, formData, {
				headers: {
					Authorization: `Token ${token}`
				},
				onUploadProgress: (progressEvent) => {
					const { total, loaded } = progressEvent;
					setState((prev: any) => {
						return {
							...prev,
							audioUploadProgress: Number(Math.round((loaded / total) * 100).toFixed(2))
						};
					});
				},
				cancelToken: source.token
			})
			.then(({ data }) => {
				setAudioResponse(data as Record<string, any>);

				setState((prev: any) => {
					return {
						...prev,
						audioUploadProgress: null
					};
				});
				setAudioUploadProgress(null);
			})
			.catch((e) => {
				console.log('error audio upload', e);
			});
	};

	return {
		uploadAudioFile
	};
}
