import React, { createContext, useReducer, useContext, useCallback } from 'react';
import { SetAudioCancelSending } from 'shared/contexts/reducers/chatInputReducer';

type AudioUploadProgressT = null | number;

type AudioResponse = Record<string, any> | null;

type SetAudioUploadProgress = {
	type: 'SET_AUDIO_UPLOAD_PROGRESS';
	payload: AudioUploadProgressT;
};

type SetResponse = {
	type: 'SET_AUDIO_RESPONSE';
	payload: AudioResponse;
};

type ContextType = {
	searchQuery: string;
	setSearchQuery: (query: string | null) => void;
	isStaff: boolean;
	setIsStaff: (type: boolean) => void;
	audioUploadProgress: AudioUploadProgressT;
	cancelAudioUpload: boolean;
	setAudioCancelSending: (isCancelling: boolean) => void;
	setAudioUploadProgress: (progress: AudioUploadProgressT) => void;
	audioResponse: AudioResponse;
	setAudioResponse: (response: AudioResponse) => void;
};

// eslint-disable-next-line @typescript-eslint/no-non-null-assertion
export const NewsContext = createContext<ContextType>(null!);
type setSearchQuery = {
	type: 'SET_SEARCH_QUERY';
	payload: string | null;
};

type setIsStaff = {
	type: 'SET_IS_STAFF';
	payload: boolean;
};

type StateType = {
	searchQuery: string | null;
	isStaff: boolean;
	audioUploadProgress: AudioUploadProgressT;
	cancelAudioUpload: boolean;
	audioResponse: AudioResponse;
};

type Actions =
	| setSearchQuery
	| setIsStaff
	| SetAudioCancelSending
	| SetAudioUploadProgress
	| SetResponse;

const reducer = (state: StateType, action: Actions): StateType => {
	switch (action.type) {
		case 'SET_SEARCH_QUERY': {
			return {
				...state,
				searchQuery: action.payload
			};
		}
		case 'SET_IS_STAFF': {
			return {
				...state,
				isStaff: action.payload
			};
		}
		case 'SET_AUDIO_CANCEL_SENDING': {
			return {
				...state,
				cancelAudioUpload: action.payload
			};
		}
		case 'SET_AUDIO_UPLOAD_PROGRESS': {
			return {
				...state,
				audioUploadProgress: action.payload
			};
		}
		case 'SET_AUDIO_RESPONSE': {
			return {
				...state,
				audioResponse: action.payload
			};
		}
		default:
			return state;
	}
};

export function NewsContextProvider({
	children
}: React.PropsWithChildren<Record<string, unknown>>) {
	const [state, dispatch] = useReducer(reducer, {
		searchQuery: null,
		isStaff: false,
		cancelAudioUpload: false,
		audioUploadProgress: null,
		audioResponse: null
	});

	const { searchQuery, isStaff, cancelAudioUpload, audioUploadProgress, audioResponse } = state;

	const setSearchQuery = useCallback((query: string | null) => {
		dispatch({ type: 'SET_SEARCH_QUERY', payload: query });
	}, []);
	const setIsStaff = useCallback((type: boolean) => {
		dispatch({ type: 'SET_IS_STAFF', payload: type });
	}, []);

	const setAudioCancelSending = useCallback(
		(isCancelling: boolean) =>
			dispatch({ type: 'SET_AUDIO_CANCEL_SENDING', payload: isCancelling }),
		[]
	);
	const setAudioUploadProgress = useCallback(
		(progress: AudioUploadProgressT) =>
			dispatch({ type: 'SET_AUDIO_UPLOAD_PROGRESS', payload: progress }),
		[]
	);
	const setAudioResponse = useCallback(
		(response: AudioResponse) => dispatch({ type: 'SET_AUDIO_RESPONSE', payload: response }),
		[]
	);

	// eslint-disable-next-line react/jsx-no-constructed-context-values
	const contextValues = {
		searchQuery,
		isStaff,
		setSearchQuery,
		setIsStaff,
		cancelAudioUpload,
		audioUploadProgress,
		setAudioCancelSending,
		setAudioUploadProgress,
		setAudioResponse,
		audioResponse
	} as ContextType;

	return <NewsContext.Provider value={contextValues}>{children}</NewsContext.Provider>;
}

export const useNewsContext = () => {
	const newsContext = useContext(NewsContext);

	if (!newsContext) throw new Error('useNewsContext must be used within the NewsContextProvider');
	return newsContext;
};
