import { useEntityActionPopup, useNotistack } from '../../../shared/hooks';
import { useAppState } from '../../../shared/state';
import { formRequest } from '../../../modules/entity/actions';
import { useDispatch } from 'react-redux';
import { NewsEntity } from '../../../shared/typings/news.types';

export function useNewsList() {
	const { companyId } = useAppState();
	const dispatch = useDispatch();
	const { showNotification } = useNotistack();
	const {
		visible: deleteVisible,
		showVisible: showDeleteVisible,
		loading: deleteLoading,
		toggleLoading: toggleDeleteLoading,
		closePopup: closeDeletePopup
	} = useEntityActionPopup<NewsEntity>();

	const handleNewsDelete = (news: NewsEntity) => {
		//show loader
		toggleDeleteLoading();
		dispatch(
			formRequest({
				entity: 'news',
				name: `AllNews`,
				method: 'delete',
				url: `/${companyId}/notifications/${news.id}/`,
				deleteData: true,
				id: news.id,
				cb: {
					success: () => {
						showNotification({ message: 'Successfully deleted', variant: 'success' });
					},
					error: (err) => {
						showNotification({ message: 'Something went wrong', variant: 'error' });
					},
					finally: () => {
						toggleDeleteLoading();
						closeDeletePopup();
					}
				},
				params: {},
				primaryKey: 'id',
				values: {}
			})
		);
	};

	return {
		deleteLoading,
		handleNewsDelete,
		showDeleteVisible,
		closeDeletePopup,
		deleteVisible
	};
}
